import React from 'react';
import Card from './Card';
export default function Cards(props) {
  // acá va tu código
  // tip, podés usar un map
  return <div>
    {
      props.map(ciudad => (
        <Card
        max ={ciudad.max}
        min = {ciudad.min}
        name = {ciudad.name}
        img = {ciudad.img}
        onClose = {ciudad.onClose}
        />
      ))
    }
  </div>
};